import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MessageService, SelectItem } from 'primeng/api';
import { NotifyService } from '../services/notify/notify.service';
import { StorageService } from '../services/storage-service.service';

@Component({
  selector: 'app-suporte',
  templateUrl: './suporte.page.html',
  styleUrls: ['./suporte.page.scss'],
})
export class SuportePage implements OnInit {

  abaAtual: string = 'faq';
  formGroup: FormGroup;
  motivos: SelectItem[];
  loading: boolean = false;


  constructor(
    private toast: MessageService,
    public formBuilder: FormBuilder,
    private notify: NotifyService,
    private storage: StorageService,
    private auth: StorageService,
  ) {
    this.formGroup = this.formBuilder.group({
      email: ['', [Validators.required, Validators.email]],
      motivo: ['', [Validators.required]],
      descricao: ['', [Validators.required, Validators.minLength(10)]],
    });
    this.motivos = [];
    this.motivos.push({ label: 'Selecione', value: '' });
    this.motivos.push({ label: 'Reportar BUG ou falha', value: '0' });
    this.motivos.push({ label: 'Requerer nova funcionalidade', value: '1' });
    this.motivos.push({ label: 'Relizar um elogio', value: '2' });
    this.motivos.push({ label: 'Outros', value: '3' });
  }

  ngOnInit() {
    const localUser = this.storage.getLocalUser();

    if (localUser) {
      const email = localUser.email;
      if (email.includes('@')) {
        this.formGroup.get('email').setValue(email);
        this.formGroup.get('email').disable({ onlySelf: true });
      }
    }

  }

  mudaAba(event) {
  }

  enviar() {
    this.loading = true;
    const email = this.formGroup.get('email').value;
    const motivo = this.formGroup.get('motivo').value;
    const descricao = this.formGroup.get('descricao').value;

    console.log(motivo, descricao);

    this.notify.contact({ email, motivo, descricao })
      .subscribe(res => {
        this.callToast('success', 'Contato enviado!', 'Agradecemos sua colaboração :D');
        this.loading = false;
      },
        error => {
          console.log(error);
          this.loading = false;
        })
  }

  callToast(severity, summary, detail) {
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

  isLogedIn() {
    return this.auth.isLogedIn();
  }

}
